import React from "react";
import { Card, Col, Container, Row } from "react-bootstrap";

import { Bestiary } from "./Cards/Bestiary"

const Divider = ({color}) => {
    <hr
        style={{
            color: color,
            backgroundColor: color,
            height: 5
        }}
    />
}

const Creature_Card = ({
        //Basic    
        creatureName, challengeRating, exp, alignment, 
        size, type, initative, senses, perception, 
        //Defenses
        armorClasses,
        hd, saves, defensiveAbilities,
        //Offenses
        speeds, meleeAttacks, rangedAttacks, specialAttacks,
        spells, space, reach,
        //Stats
        stats, bab, cmd,
        feats, skills, racialMods, languages,
        //Abilities
        specialQualities, specialAbilities,
        //Lore
        environment, organization, treasure, desc
    }) => {
    if(!creatureName) return <div />

    let hp = 0; // Implement auto-calc from HD

    const parseMelee = (atks) => {
        if(!atks.length > 0) return <div />
        let a = atks.map((data, key) => {
            return(
                <Row>
                    {data[0]} +{data[1]} ({data[2]} {data[3]})
                </Row>
            );
        })
        return(
            <Row>{a}</Row>
        )
    }
    const parseRanged = (atks) => {
        if(!atks.length > 0) return <div />
        let a = atks.map((data, key) => {
            return(
                <Row>
                    {data[0]} +{data[1]} {data[2]}ft. ({data[3]} {data[4]})
                </Row>
            );
        })
        return(
            <Row>{a}</Row>
        )
    }
    const parseSpecial = (atks) => {
        if(!atks.length > 0) return <div />
        let a = atks.map((data, key) => {
            return(
                <Row>
                    {data[0]} +{data[1]} ({data[2]} {data[3]})
                </Row>
            );
        })
        return(
            <Row>{a}</Row>
        )
    }
    const parseSpells = (spellList) => {
        return <div />
    }

    return (
    <>
        <Card className="creature-card">
            <Card.Title>{creatureName}</Card.Title>
            <Container>
                <Row>
                    <Col><b>CR</b>: {challengeRating}</Col>
                    <Col><b>XP</b>: {exp}</Col>
                </Row>
                <Row>{alignment} {size} {type}</Row>
                <Row>
                    <Col>{initative}</Col>
                    <Col><b>Senses</b>: {senses.join(", ")}</Col>
                    <Col><b>Perception</b>: {perception}</Col>
                </Row>
            </Container>

            <Divider color="gray"/>
            <Card.Subtitle>Offenses</Card.Subtitle>
            <Container> 
                <Row>
                    <Col><b>AC</b>: {armorClasses[0]}</Col>
                    <Col><b>Touch</b>: {armorClasses[1]}</Col>
                    <Col><b>Flat-footed</b>: {armorClasses[2]}</Col>
                    <Col>({armorClasses[4]})</Col>
                </Row>
                <Row>
                    <Col><b>HP</b>: {hp} ({hd})</Col>
                </Row>
                <Row>
                    <Col><b>Fort</b>: {saves[0]}</Col>
                    <Col><b>Ref</b>: {saves[1]}</Col>
                    <Col><b>Will</b>: {saves[2]}</Col>
                </Row>
                <Row>
                    <Col><b>Defensive Abilities</b>: {defensiveAbilities.join(", ")}</Col>
                </Row>
            </Container>

            <Divider color="gray" />
            <Card.Subtitle>Defenses</Card.Subtitle>
            <Container>
                <Row>
                    { speeds.map((data, key) => {
                        return(
                            <Col><b>{data[0]}</b>: {data[1]}ft.</Col>
                        );
                    })}
                </Row>
                <Row>
                    <Col><b>Space</b>: {space}</Col>
                    <Col><b>Reach</b>: {reach}</Col>
                </Row>
                {parseMelee(meleeAttacks)}
                {parseRanged(rangedAttacks)}
                {parseSpecial(specialAttacks)}
                {parseSpells(spells)}
            </Container>

            <Divider color="gray" />
            <Card.Subtitle>Statistics</Card.Subtitle>
            <Container></Container>
        </Card>
    </>
    );
}

export const Creature_Cards = () => {
    return (
        <div>
            { Bestiary.map((data, key) => {
                return (
                    <div className="creature-card">
                        <Creature_Card 
                        creatureName={data.creatureName}
                        challengeRating={data.challengeRating}
                        exp={data.exp}
                        alignment={data.alignment}
                        size={data.size}
                        type={data.type}
                        initative={data.initative}
                        senses={data.senses}
                        perception={data.perception}
                        //Defenses
                        armorClasses={data.armorClasses}
                        hd={data.hd}
                        saves={data.saves}
                        defensiveAbilities={data.defensiveAbilities}
                        //Offenses
                        speeds={data.speeds}
                        meleeAttacks={data.meleeAttacks}
                        rangedAttacks={data.rangedAttacks}
                        specialAttacks={data.specialAttacks}
                        spells={data.spells}
                        space={data.space}
                        reach={data.reach}
                        //Stats
                        stats={data.stats}
                        bab={data.bab}
                        cmd={data.cmd}
                        feats={data.feats}
                        skills={data.skills}
                        racialMods={data.racialMods}
                        languages={data.languages}
                        //Abilities
                        specialQualities={data.specialQualities}
                        specialAbilities={data.specialAbilities}
                        //Lore
                        environment={data.environment}
                        organization={data.organization}
                        treasure={data.treasure}
                        desc={data.desc}
                        />
                    </div>
                );
            })}
        </div>
    );
}